import React from 'react';
import { Card } from 'primereact/card';
import { Avatar } from 'primereact/avatar';
import profileImage from '../../assets/profile.jpg';
import 'primeflex/primeflex.css';

export default function Sobre() {
  const header = <h1 className='mt-0'>Aplicao de Gesto de Empresas Externas e Parceiros</h1>;
  const footer = (
    <div className="flex flex-column align-items-center">
      <Avatar image={profileImage} size="xlarge" shape="circle" />
      <label className="mt-3">Desenvolvido por Kay Haufe.</label>
    </div>
  );

  return (
    <div className="sobre flex justify-content-center align-items-center">
      <Card title={header} footer={footer}>
        <section>
          <h2>Viso Geral</h2>
          <p>
            A aplicao de <strong>Gesto de Empresas Externas e Parceiros</strong>  uma soluo voltada para o gerenciamento
            e administrao de dados de empresas parceiras e colaboradores. O sistema permite que os usurios realizem o
            cadastro, edio e excluso de empresas externas, com funcionalidades adicionais como filtragem de dados,
            paginao e suporte a operaes CRUD (Create, Read, Update, Delete).
          </p>
        </section>

        <section>
          <h2>Tecnologias Utilizadas</h2>
          <ul>
            <li>React para a construo da interface do usurio</li>
            <li>PrimeReact para componentes ricos e estilizao</li>
            <li>Integrao com APIs RESTful para CRUD de dados</li>
            <li>Login com armazenamento em cookie e local storage</li>
          </ul>
        </section>

        <section>
          <h2>Funcionalidades Principais</h2>
          <ul>
            <li>Cadastro e edio de empresas externas e parceiros</li>
            <li>Suporte a filtragem e busca global de empresas</li>
            <li>Paginao dinmica para grandes volumes de dados</li>
            <li>Feedbacks interativos com Toasts para aes bem-sucedidas</li>
          </ul>
        </section>
      </Card>
    </div>
  );
}
